import {Component, OnInit, ViewChild} from '@angular/core';
import {FileUploader} from 'ng2-file-upload';
import {AuthService} from '../services/auth.service';
import {environment} from '../../environments/environment';
import {GalleryService} from '../services/gallery.service';
import {Image} from '../models/image.model';
import {GALLERY_CONF, GALLERY_IMAGE, NgxImageGalleryComponent} from 'ngx-image-gallery';
import {Response} from '@angular/http';
import {NotificationsService} from 'angular2-notifications';

@Component({
    selector: 'app-home',
    templateUrl: './gallery.component.html',
    styleUrls: ['./gallery.component.css']
})
export class GalleryComponent implements OnInit {
    @ViewChild(NgxImageGalleryComponent) ngxImageGallery: NgxImageGalleryComponent;

    // gallery configuration
    public conf: GALLERY_CONF = {
        imageOffset: '0px',
        showDeleteControl: false,
        inline: false,
        closeOnEsc: true,
        reactToKeyboard: true,
        reactToRightClick: true,
        showImageTitle: true,
    };

    // gallery images
    public images: GALLERY_IMAGE[] = [];
    public imageList: Array<Image> = [];
    public viewPortItems: Array<Image> = [];

    public uploader: FileUploader;
    public hasBaseDropZoneOver: boolean;
    private URL: string;
    public progress: number = 50;
    readonly environment = environment;
    private fileCount: number = 0;

    constructor(readonly _authService: AuthService,
                private _galleryService: GalleryService,
                private notificationService: NotificationsService) {
        this.URL = environment.urls.rest + 'api/gallery';

        this.uploader = new FileUploader({
            url: this.URL,
            isHTML5: true,
            authToken: _authService.token,
            removeAfterUpload: true
        });

        this.conf.showDeleteControl = this._authService.isLoggedIn;
        this.hasBaseDropZoneOver = true;
    }

    ngOnInit() {
        this.uploader.onAfterAddingFile = (file) => {
            file.withCredentials = false;
        };

        this.uploader.onSuccessItem = () => this.fileCount++;

        this.uploader.onCompleteAll = () => {
            this.notificationService.success('Success', this.fileCount + ' images uploaded');
            this.fileCount = 0;
            this.getImages();
        }

        this.getImages();
    }

    private getImages() {
        this._galleryService.getImages({skip: 0})
            .subscribe((imageList: Array<Image>) => {
                this.imageList = [...imageList];

                this.images = [...this.imageList.map((i: Image) => {
                    return {
                        url: environment.urls.rest + i.location,
                        altText: i.title,
                        id: i.id,
                        title: i.title,
                        thumbnailUrl: environment.urls.rest + i.thumb,
                    };
                })];
            });
    }

    /**
     * Event handler for storing the dropzone when a file has been dragged over
     * @param e
     * @return void
     */
    public fileOverBase(e: any): void {
        this.hasBaseDropZoneOver = e;
    }

    // open gallery
    openGallery(index: number = 0) {
        this.ngxImageGallery.open(index);
    }

    // callback on user clicked delete button
    deleteImage(index) {
        this._galleryService.deleteImage(this.images[index])
            .then((response: Response) => {
                this.notificationService.success('Success', this.images[index].title + ' has been deleted');

                this.images.splice(index, 1);
                this.images = [...this.images];

                this.imageList.splice(index, 1);
                this.imageList = [...this.imageList];

                if (!this.images.length) {
                    this.ngxImageGallery.close();
                }
            })
            .catch((err: any) => {
                console.error(err);
                this.notificationService.error('Error', 'Unable to remove ' + this.images[index].title);
            });
    }
}